@import url('https://fonts.googleapis.com/css?family=Montserrat');

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: #fff;
}

* {
    margin: 0;
    padding: 0;
    border: 0
}

ol, ul {
    list-style: none
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main, audio, canvas, video, svg {
    display: block
}

.site-header {
    position: fixed;
    width: 100%;
    z-index: 10;
    top: 0;
    left: 0;
    background: #1A1F21
}
/****FADE IN ****/
.page-loaded .fade-in, .page-loaded.fade-in {
    opacity: 1
}
.fade-in {
    opacity: 0;
    -webkit-transition: opacity 1s ease;
    -o-transition: opacity 1s ease;
    transition: opacity 1s ease
}
.page-loaded .fade-in, .page-loaded.fade-in {
    opacity: 1
}

/****FADE IN ****/

*, *:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}

.p a, p a {
    text-decoration: underline
}

.p + .btn, .p + .icon-link, p + .btn, p + .icon-link {
    margin-top: 30px
}

@media (max-width: 599px) {
    .p, p {
        font-size: 1.5rem
    }
}

.p-lg, .lead {
    font-size: 2.0rem;
    line-height: 1.5;
    font-weight: 400
}

@media (min-width: 850px) {
    .p-lg, .lead {
        font-size: 2.4rem
    }
}
.profile-intro{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background: #22292C;
    overflow: hidden;
    z-index: 2;
    padding: 60px 0 150px
}
.container-wide {
    width: 92%;
    margin: 0 auto;
    max-width: 1005px;
    -webkit-transition: max-width .4s ease-out;
    -o-transition: max-width .4s ease-out;
    transition: max-width .4s ease-out;
    max-width: 1440px
}

.site-header .container-wide {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 60px;
    position: relative;
    -webkit-transition: height .4s ease-out;
    -o-transition: height .4s ease-out;
    transition: height .4s ease-out
}

.site-navigation {
    text-align: center;
    padding: 40px 0
}

.site-navigation a {
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
    font-size: 1rem;
    line-height: 2;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #fff;
    position: relative;
    padding: 5px
}

.site-navigation a:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    z-index: 1;
    opacity: 1;
    background: -webkit-gradient(linear, left top, right top, from(#E67207), color-stop(50%, #EF4E7B), to(#A166AB));
    background: -webkit-linear-gradient(left, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
    background: -o-linear-gradient(left, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
    background: linear-gradient(to right, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
    -webkit-transform-origin: center;
        -ms-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scaleX(0);
        -ms-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: -webkit-transform .4s ease-out;
    transition: -webkit-transform .4s ease-out;
    -o-transition: transform .4s ease-out;
    transition: transform .4s ease-out;
    transition: transform .4s ease-out, -webkit-transform .4s ease-out
}

.site-navigation li.is-active a:after, .site-navigation a:hover:after {
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1)
}

@media (min-width: 850px) {

    .site-header-panel {
        width: 50%
    }

    .site-header .container-wide {
        height: 70px
    }

    .mobile-menu-trigger {
        display: none !important;
    }

    .screen {
        display: none !important
    }

    .site-navigation {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: end;
            justify-content: flex-end;
        -ms-flex-align: center;
            align-items: center;
        padding: 0
    }

    .site-navigation ul {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: justify;
            justify-content: space-between;
        width: 100%;
        max-width: 445px
    }

    .site-navigation ul li {
        -ms-flex-positive: 1;
            flex-grow: 1
    }

    .site-navigation a {
        font-size: 1.2rem
    }

    .site-navigation a {
        font-size: 1.2rem
    }

    .site-header .mobile-only {
        display: none
    }



}


@media (max-width: 849px) {
    .site-navigation {
        position: fixed;
        bottom: 40px;
        right: 3%;
        -webkit-transform: scale(0);
            -ms-transform: scale(0);
                transform: scale(0);
        z-index: 12
    }

    .site-navigation li {
        opacity: 0;
        display: block;
        -webkit-transform: translate3d(4px, 0px, 0);
                transform: translate3d(4px, 0px, 0);
        -webkit-transition: all .3s ease;
        -o-transition: all .3s ease;
        transition: all .3s ease;
        text-align: left
    }

    .site-navigation li + li {
        margin-top: 10px
    }

    .site-navigation a {
        text-decoration: none;
        padding: 4px 8px;
        color: white
    }
}

@media (min-width: 1025px) {
    .logo {
        width: 25%
    }

    .logo a {
        max-width: 208px
    }

    .site-header-panel {
        width: 50%
    }

    .site-navigation ul {
        max-width: 640px
    }

    .site-navigation a {
        text-decoration: none;
    }
}

.logo {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.logo a {
    position: relative;
    max-width: 150px;
    width: 90%;
    display: block;
    color: #fff;
    text-decoration: none;
    font-style: normal;
    -webkit-transition: max-width .4s ease-out;
    -o-transition: max-width .4s ease-out;
    transition: max-width .4s ease-out
}

.logo a img {
    width: 100%;

}

/******** START MOBILE MENU***********/
.mobile-menu-trigger {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-linear-gradient(315deg, #43C6AC 0%, #5073B8 100%);
    background: -o-linear-gradient(315deg, #43C6AC 0%, #5073B8 100%);
    background: linear-gradient(135deg, #43C6AC 0%, #5073B8 100%);
    -webkit-box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.3);
            box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.3);
    position: fixed;
    z-index: 13;
    bottom: 15px;
    right: 15px
}

.mobile-menu-trigger span {
    width: 25px;
    height: 2px;
    display: block;
    background: white
}

.mobile-menu-trigger span + span {
    margin-top: 4px
}

.mobile-menu-trigger:hover {
    cursor: pointer
}

.menu-is-active .mobile-menu-trigger span {
    background: #1A1F21
}

.menu-is-active .site-navigation {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1)
}

.menu-is-active .site-navigation li {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0)
}

.menu-is-active .site-navigation li:first-child {
    -webkit-transition-delay: 0.08s;
         -o-transition-delay: 0.08s;
            transition-delay: 0.08s
}

.menu-is-active .site-navigation li:nth-child(2) {
    -webkit-transition-delay: 0.07s;
         -o-transition-delay: 0.07s;
            transition-delay: 0.07s
}

.menu-is-active .site-navigation li:nth-child(3) {
    -webkit-transition-delay: 0.05s;
         -o-transition-delay: 0.05s;
            transition-delay: 0.05s
}

.menu-is-active .site-navigation li:nth-child(4) {
    -webkit-transition-delay: 0.03s;
         -o-transition-delay: 0.03s;
            transition-delay: 0.03s
}

.menu-is-active .site-navigation li:nth-child(4) {
    -webkit-transition-delay: 0.01s;
         -o-transition-delay: 0.01s;
            transition-delay: 0.01s
}

/****SCREEN CLICK()***/
.modal-screen, .screen {
    display: inline;
    position: fixed;
    z-index: 9;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 1;
    background: rgba(26, 31, 33, 0.96)
}

.modal-screen {
    z-index: 11
}

/********* END MOBILE MENU ************/

.text-gradient {
    display: inline-block;
    background-image: -webkit-gradient(linear, left top, right top, from(#F79533), color-stop(15%, #F37055), color-stop(30%, #EF4E7B), color-stop(44%, #A166AB), color-stop(58%, #5073B8), color-stop(72%, #1098AD), color-stop(86%, #07B39B), to(#6DBA82));
    background-image: -webkit-linear-gradient(left, #F79533 0%, #F37055 15%, #EF4E7B 30%, #A166AB 44%, #5073B8 58%, #1098AD 72%, #07B39B 86%, #6DBA82 100%);
    background-image: -o-linear-gradient(left, #F79533 0%, #F37055 15%, #EF4E7B 30%, #A166AB 44%, #5073B8 58%, #1098AD 72%, #07B39B 86%, #6DBA82 100%);
    background-image: linear-gradient(90deg, #F79533 0%, #F37055 15%, #EF4E7B 30%, #A166AB 44%, #5073B8 58%, #1098AD 72%, #07B39B 86%, #6DBA82 100%);
    background-size: cover;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent
}



